div.job_listings {
	form.job_filters {
		aside.job-types {
			@include displayFlex(row);
			.job-types__list {
				@include displayFlex(row, space-evenly, center);
				@include respond(iPhone) {
					@include displayFlex(column);
				}
			}
		}
	}
	ul.job_listings {
		.job_listing {
			&:not(:last-child) {
				margin-bottom: 1.5em;
			}
			&__container {
				transition: $transition--standard;
				@include displayFlex(row, space-between, center, nowrap);
				@include respond(Tablet) {
					@include displayFlex(column);
				}
				@include sparkBorder($color-grey--dark);
				&:hover {
					@include cssHover;
				}
			}
			&__company-logo {
				margin-right: 0.5em;
			}
			&__summary {
				@include displayFlex(column, flex-start, flex-start);
				flex-grow: 3;
				&--title {
					@include displayFlex(column-reverse);
					margin: unset;
					span.job-manager-applications-applied-notice {
						margin-left: auto;
					}
				}
				&--company {
					&-name {
						font-family: $font-body;
						color: black;
						margin: 0;
						font-size: 1em;
					}
					&-tagline {
						color: $color-grey--dark;
						max-width: 80%;
						font-style: italic;
					}
				}
			}
			&__details {
				align-self: flex-start;
				@include displayFlex(column);
				@include respond(Tablet) {
					margin-top: 1em;
				}
				&--location {
					color: black;
					font-weight: 700;
				}
				&--meta {
					list-style: none;
					display: block;
					color: black;
					margin: 0;
					li {
						display: block;
					}
				}
			}
		}
	}
	a.load_more_jobs {
		margin-top: 1em;
		@include sparkBorder($color-secondary);
		background-color: white;
		transition: all 0.2s ease;
		&:hover {
			background-color: $color-secondary;
			border: 3px solid transparent;
			color: white;
			@include cssHover;
		}
	}
}
